const BASEURL_ = 'http://localhost:8080/'


export function fetch(uri, data, methods = 'POST', header) {
	return new promise({
		url: BASEURL_ + uri,
		methods: methods,
		data: {
			...data,
			...{}
		},
		success(res) {
			if (res.data.errno === 0) {
				resolve(res.data);
			} else {
				reject(res.data);
			}
		},
		fail(err) {
			reject(err);
		}
	})
}
